.backdrop {
  width: 100vw;
  height: 100vh;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 1200;

  display: flex;
  justify-content: center;
  align-items: center;

  background: rgba(97, 97, 97, 0.6);
  backdrop-filter: blur(4px);
  overflow-y: scroll;
}

.modal {
  min-height: 200px;
  min-width: 200px;
  border-radius: 20px;

  position: absolute;
  top: 10%;

  background-color: #fff;
}

.closeBtn {
  width: 24px;
  height: 24px;
  padding: 0;
  border: none;
  position: absolute;
  right: 12px;
  top: 12px;
  line-height: 0;
  background: transparent;
}

.closeIcon {
  fill: var(--blue);
  transition: transform 0.3s ease;

  &:hover,
  &:focus {
    fill: #00a2ff;
    transform: rotate(180deg);
  }
}

@media screen and (min-width: 768px) {
  .modal {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  .closeBtn {
    right: 24px;
    top: 24px;
  }
}